.tf-number input {
  @include field;
}

.tf-number-field {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}

.tf-number-button-group {
  @include button;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  padding: 0;

  .tf-button-number {
    display: flex;
    align-items: center;
    justify-content: center;
    line-height: 0;
    padding: $button-padding;
    height: 50%;

    &:last-child {
      border-top: $button-border;
    }

  }
  
}
